	import {
			mapState,
			mapMutations
	} from 'vuex';

	export default {
		data() {
			return {
        src: '', //上传图片
				userName: "", //昵称
			}
    },
    computed: {
			...mapState(['userMsg'])
    },
    onLoad(option) {
      let { avatar } = option;
      if (avatar) {
        this.src = avatar;
      } else {
        this.src = this.userMsg.avatar
      }
    },
		mounted() {
      this.userName = this.userMsg.nickname;
    },
		methods: {
      upload() {
        uni.chooseImage({
          count: 1, // 默认9
          sizeType: ['compressed'], // 可以指定是原图还是压缩图，默认二者都有
          sourceType: ['album', 'camera'], // 可以指定来源是相册还是相机，默认二者都有
          success(res) {
            const src = res.tempFilePaths[0];

            uni.redirectTo({
              url: '../../upload/upload?src=' + src
            });
          }
        });
      },
			editData() {
				//保存个人资料

			}
		}
	}
